@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.modal {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.60);
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
}

.modalContainer {
    width: 50%;
    min-height: 50vh;
    max-height: 100vh;
    background: #fff;
    border-radius: 10px;
    padding: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.modalHeader {
    width: 100%;
    border-bottom: solid 1px #bdc3c7;
    display: flex;
    justify-content: center;
    padding: 0 0 16px 0;
}

.modalHeader img {
    width: 20%;
}

.modalBody {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: space-evenly;
    text-align: center;
    padding: 16px 0;
}

.infoRow {
    /* width: 50%; */
    display: flex;
    align-items: baseline;
}

.infoRow h3 {
    color: #424646;
}

.infoRow h4 {
    padding: 0 4px;
}

.btnClose {
    border: none;
    background: #e74c3c;
    padding: 12px;
    color: #ffff;
    cursor: pointer;
    border-radius: 10px;
    font-weight: bolder;
    font-size: 12pt;
    display: flex;
    align-self: flex-end;
}
h2 {
    padding: 0;
    margin: 0;
    text-transform: capitalize;
}

h4 {
    padding: 0;
    margin: 0;
    color: #7f8c8d;
    text-transform: capitalize;
}

p {
    width: 80%;
}
.headerTop-search-time {
    display: flex;
    width: 50%;
    padding: 0 20px;
    align-items: center;
    justify-content: space-between;
}

.headerTop-searc {
    height: 30px;
    width: 100%;
    background: #494f5234;
    color: #2c3e50;
    border: none;
    padding: 8px;
    outline: none;
    border-radius: 10px;
    box-shadow: 0 0 4px white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
}

.headerTop-searc::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #2c3e50;
    font-family: Arial, Helvetica, sans-serif;
}

.headerTop-searc:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #2c3e50;
    font-family: Arial, Helvetica, sans-serif;
    opacity: 1/* esto es porque Firefox le reduce la opacidad por defecto */
    ;
}

.headerTop-searc::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #2c3e50;
    font-family: Arial, Helvetica, sans-serif;
    opacity: 1;
}

.headerTop-searc:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #2c3e50;
    font-family: Arial, Helvetica, sans-serif;
}

.icon-search {
    color: white;
    font-size: 30px;
    margin-right: 5px;
}
.container {
    width: 100%;
    height: 100vh;
    max-width: 20%;
    max-height: 50vh;
    background: #16ade1;
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 12px;
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.50);
    cursor: pointer;
}

.imageContainer {
    width: 150px;
    height: 150px;
    overflow: hidden;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    position: relative;
    top: 20px;
    background: #ecf0f1;
    padding: 12px;
    box-sizing: border-box;
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.30);
}

.imageContainer img {
    width: 70%;
}

.infoContainer {
    width: 100%;
    height: calc(100% - 150px);
    background: #ecf0f1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
body {
    font-family: 'Poppins', sans-serif;
}

.containerGeneral {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
}

.title {
    margin: 16px auto;
    font-size: 36pt;
    color: #2c3e50;
    border-bottom: 1px solid #2c3e50;
}

.containerCards {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}
